* {
  font-family: Calibri, Herlevtica, Arial;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2em;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 6px;
  margin-right: 6px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
  font-size: 1em;
}

.card img {
  max-height: 170px;
}
